@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
body {
  background-color: #22222a;
  color: #f0f0f0;
  font-family: 'Roboto Mono', sans-serif;
}
.card {
  border-color: #aaa;
  margin-bottom: 2rem;
}
.card .card-body {
    background: #44444a;
}
.card .card-header {
    background: #33333a;
    border-bottom: 1px solid #aaa;
}
.card .card-footer {
    background: #33333a;
}
.btn {
  border: 0;
  font-size: 0.75rem;
}
.btn:focus,
.btn-danger:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
}
.btn.btn-danger {
  background: #d88888;
  color: #222;
}
.btn.btn-danger:not(.disabled):not(:disabled):active {
  background: #b66666;
}
.text-blue {
  color: #8888d8;
}
.text-green {
  color: #66a666;
}
.text-red {
  color: #d88888;
}
.text-yellow {
  color: #d8d888;
}

.table tbody tr td {
  border: 0;
  padding: 0;
}
.info-key {
  width: 50%;
}
.info-value {
  text-align: left;
}
.quest-description {
  font-size: 0.875rem;
}

.header {
  margin: 1rem 0;
}
.header .header-brand {
    display: inline-block;
}
.nav-item a {
  color: #f0f0f0;
}
.navbar-nav {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.navbar-nav li {
    margin-left: 3rem;
}

.modal-content {
  border-color: #aaa;
  background: #44444a;
}
.modal-content .col-12:not(:last-of-type) {
    margin-bottom: 2rem;
}
.modal-header {
  background: #33333a;
}
.modal-header button.close {
    color: #f0f0f0;
    text-shadow: none;
    opacity: 1;
}

.reset-btn {
  display: block;
  margin: 0 auto;
}

.informant-card {
  margin-bottom: 1rem;
}
.informant-card .description {
    font-size: 0.75rem;
    font-style: italic;
}

.unread-counter {
  font-weight: bold;
}

.card-message {
  margin-bottom: 1rem;
}
.card-message .card-header {
    cursor: pointer;
}
.card-message .card-header .oi {
      margin-right: 1rem;
}
.card-message .card-header .oi-envelope-closed {
      color: #66a666;
}
.card-message .card-header .oi-envelope-open {
      color: #fff;
}
